body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card-form-wrapper {
  width: 800px;
  margin: 150px auto;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.card-form-wrapper h1 {
  text-align: center;
  width: 100%;
  font-size: 30px;
  font-weight: 500;
}
.card-form {
  width: 600px;
  margin: 0 auto;
  margin-bottom: 50px;
}
.card-form h3 {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 10px;
  margin-top: 20px;
}
.card-form span {
  display: -webkit-flex;
  display: flex;
  padding: 4px;
  padding-left: 12px;
  font-size: 12px;
  color: rgb(218, 102, 98);
}
.card-form #cardName, #cardNumber {
  width: 100%;
  padding: 5px 10px;
}
.card-form #month {
  width: 40px;
  margin-right: 10px;
  padding: 5px 10px;
}
.card-form #year {
  padding: 5px 10px;
  width: 70px;
  margin-right: 10px;
}
.card-form #cvc {
  padding: 5px 10px;
  width: 200px;
}

.submitButton {
  margin-top: 20px;
  color: white;
  font-size: 12px;
  border-radius: 4px;
  padding: 5px 10px;
  background-color: rgb(0, 123, 177);
  cursor: pointer;
}
.message {
  margin-top: 30px;
  text-align: center;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
}

